
body {
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #34495e;
  margin: 0;
  background:#728EB2;
}
h5 {
  margin-bottom: 3px;
  margin-top: 10px;
  font-size: 19px;
}
#rules {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.testArea {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.sidebar .item {
  position: relative;
  right: -1px;
  font-size: 13px;
  width: 23px;
  height: 81px;
  margin: 4px;
  margin-right: 0;
  border-top-left-radius: 11px;
  background: #d0d0d0;
  border-bottom-left-radius: 12px;
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-shadow: -1px 1px 3px rgba(0,0,0,.4);
          box-shadow: -1px 1px 3px rgba(0,0,0,.4);
  cursor: pointer;
}
.sidebar .item.file {
  background: #e0e0e0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.sidebar .item.file.new {
  height: 30px;
  font-weight: bold;
  font-size: 20px;
  -webkit-writing-mode: initial;
      -ms-writing-mode: initial;
          writing-mode: initial;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.sidebar .item.save {
  background: #d22222;
  color: #fff;
  left: 0;
  margin-top: 30px;
  font-size: 16px;
  position: fixed;
  bottom: 10px;
}
.sidebar .item.save:hover {
  background: #e33333;
}
.sidebar .item:hover{
  background: #8fadd2
}
.sidebar .item.active {
  z-index: 20;
  color: #fff;
  background: #4b90e2;
  border-left: 1px solid #aeaee6;
  border-top: 1px solid #aeaee6;
  border-bottom: 1px solid #aeaee6;
}
.fileArea {
  padding: 10px;
  color: #fff;
  background: #4A90E2;
  border-right: 1px solid #aeaee6;
  border-left: 1px solid #aeaee6;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
  width: 500px;
}

.rule-data {
  margin: 10px;
}
#rules select {
  color: inherit;
  font-size: inherit;
  -webkit-appearance: none;
  border: none;
  border-bottom: 1px dashed;
  border-radius: 0;
  background: none;
  height: 30px;
  cursor: pointer;
}
#rules select:hover {
  background: rgba(0,0,0,.2);
}
#rules select:focus {
  outline: none;
}
.addBtn {
  height: 30px;
  border-radius: 9px;
  background: #34a1ff;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  -webkit-box-shadow: 0px 0px 4px rgba(0,0,0,.4);
          box-shadow: 0px 0px 4px rgba(0,0,0,.4);
  margin: 10px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  cursor: pointer;
}
.addBtn:hover {
  background: #44b1ff;
}
.nck {
  display: inline-block;
  -webkit-appearance: none;
  width: 1.4em;
  height: 1.4em;
  background: #fff;
  border: none;
  border-radius: 50%;
  vertical-align: middle;
}
label:hover .nck {
  -webkit-box-shadow: inset 0 0 11px #0b09a0;
          box-shadow: inset 0 0 11px #0b09a0;
}
.nck:checked {
  border: 3px solid #fff;
  background: #4a4aff;
}
.addCond {
  border: 1px dashed #fff;
  display: block;
  margin: 10px 0;
  width: 70px;
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
}
.addCond:hover {
  background: rgba(0,0,0,.2);
}
.editArea {
  color: #fff;
  width: 500px;
  background: #4A90E2;
  border-right: 1px solid #aeaee6;
  border-left: 1px solid #aeaee6;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
  z-index: 10;
}

.xeditor { 
  background: #fff;
  display: block;
  overflow-y: hidden;
  padding: 10px;
  color: #000;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  font-size: 14px;
  border-radius: 6px;
  -webkit-box-shadow: 2px 2px 8px rgba(0,0,0, .3);
          box-shadow: 2px 2px 8px rgba(0,0,0, .3);
  border: 0;
  resize: none;
}
.xeditor:focus {
  outline: none;
}

#tests {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.rulePanel {
  margin: 10px;
  padding: 10px;
  border-bottom: 1px solid;
}
.textPanel {
  margin: 10px
}
.passIcon {
  height: 15px;
  border-radius: 15px;
  color: #fff;
  background: #070;
  vertical-align: 2px;
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
  line-height: 15px;
  text-align: center;
}
.errIcon {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  color: #fff;
  background: #f00;
  vertical-align: 2px;
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
  line-height: 15px;
  text-align: center;
}
.errpanel b {
  color: #ff0000;
  font-weight: bold;
  display: inline-block;
  padding: 0 2px;
  text-shadow: 1px 1px rgba(146, 146, 146, 0.34);
}
.passPanel b {
  color: #39ce39;
  font-weight: bold;
  display: inline-block;
  padding: 0 2px;
  text-shadow: 1px 1px rgba(146, 146, 146, 0.34);
}
.respanel {
  background: #eee;
  margin: 10px;
  padding: 10px;
  position: relative;
  -webkit-box-shadow: 0 0 2px;
          box-shadow: 0 0 2px;
}
.respanel.error {
  background: #fee;
}
.respanel.error:after {
  content: "\5F02\5E38";
  position: absolute;
  right: 18px;
  color: #fff;
  background: #a22;
  top: 0;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 4px 20px;
  opacity: .7;
}
.respanel.pass:after {
  content: "\901A\8FC7";
  position: absolute;
  right: 18px;
  color: #fff;
  background: #2a2;
  top: 0;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 4px 20px;
  opacity: .7;
}
.diff {
  margin: 10px;
  padding: 10px;
  border: 1px dashed #9e9e14;
  background: #f0fbe0;
}

#mutiTest {
  font-size:12px;
}
.line {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cell {
  background: #fff;
  margin: 2px;
  width: 30px;
  text-align: center;
}
.line.err .cell{
  background: #faa;
}
.line.pass .cell{
  background: #afa;
}
.cellFull {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}
